// features/auth/api/fetchRegisterUser.ts

type RegisterUserResponse = {
  name: string;
  // API
};

export async function fetchRegisterUser(accessToken: string): Promise<string> {
  console.log('[fetchRegisterUser] called');

  if (!accessToken) {
    console.error('[fetchRegisterUser]  ');
    throw new Error('');
  }

  console.log(
    '[fetchRegisterUser] : accessToken() ',
    accessToken + '...',
  );
  const fetchUrl =
    'https://aichan-container-apps.agreeablehill-a0330681.japaneast.azurecontainerapps.io/api/register_user?';

  let response: Response;
  try {
    response = await fetch(fetchUrl, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({}),
    });
  } catch (err) {
    console.error('[fetchRegisterUser] fetch:', err);
    throw new Error('API: ' + err);
  }

  console.log('[fetchRegisterUser]  status:', response.status);

  const text = await response.text();
  console.log('[fetchRegisterUser]  body:', text);

  if (response.status === 409) {
    console.warn('[fetchRegisterUser]  (409)');
    throw new Error('409: ');
  }

  if (!response.ok) {
    console.error('[fetchRegisterUser] NG:', response.status, text);
    throw new Error(text);
  }

  let data: RegisterUserResponse;
  try {
    data = JSON.parse(text) as RegisterUserResponse;
  } catch (e) {
    console.error('[fetchRegisterUser] JSON:', e, text);
    throw new Error('APIJSON');
  }

  console.log('[fetchRegisterUser]  data:', data);

  return data.name;
}
